<template>
  <div id="formulario-persona">
    <form @submit.prevent="enviarFormulario">
      <div class="container">
        <div class="row">
          <div class="col-md-4">
            <div class="form-group">
              <label>Nombre</label>
              <input
                ref="nombre"
                v-model="persona.nombre"
                type="text"
                class="form-control"
                :class="{ 'is-invalid': procesando && nombreInvalido }"
                @focus="resetEstado"
                @keypress="resetEstado"
              />
            </div>
          </div>
          <div class="col-md-4">
            <div class="form-group">
              <label>Apellido</label>
              <input
                v-model="persona.apellido"
                type="text"
                class="form-control"
                :class="{ 'is-invalid': procesando && apellidoInvalido }"
                @focus="resetEstado" />
            </div>
          </div>
          <div class="col-md-4">
            <div class="form-group">
              <label>Email</label>
              <input
                v-model="persona.email"
                type="email"
                class="form-control"
                :class="{ 'is-invalid': procesando && emailInvalido }"
                @focus="resetEstado"
              />
            </div>
          </div>
        </div>
        <div class="row mt-3">
          <div class="col-md-4">
            <div class="form-group">
              <button class="btn btn-primary">Añadir persona</button>
            </div>
          </div>
        </div>

        <div class="container mt-3">
          <div class="row">
            <div class="col-md-12">
              <div
                v-if="error && procesando"
                class="alert alert-danger"
                role="alert"
              >
                Debes rellenar todos los campos!
              </div>
              <div v-if="correcto" class="alert alert-success" role="alert">
                La persona ha sido agregada correctamente!
              </div>
            </div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "formulario-persona",
  data() {
    return {
      // Variables de estado
      procesando: false,
      correcto: false,
      error: false,
      persona: {
        nombre: "",
        apellido: "",
        email: "",
      },
    };
  },
  methods: {
    enviarFormulario() {
      this.procesando = true;
      this.resetEstado();

      // Comprobamos si hay errores
      if (this.nombreInvalido || this.apellidoInvalido || this.emailInvalido) {
        this.error = true;
        return;
      }

      this.$emit("add-persona", this.persona);
      this.$refs.nombre.focus();

      this.error = false;
      this.correcto = true;
      this.procesando = false;

      // Restablecemos el valor de las variables
      this.persona = {
        nombre: "",
        apellido: "",
        email: "",
      };
    },
    resetEstado() {
      this.correcto = false;
      this.error = false;
    },
  },
  computed: {
    nombreInvalido() {
      return this.persona.nombre.length < 2;
    },

    apellidoInvalido() {
      return this.persona.apellido.length < 2;
    },

    emailInvalido() {
      return this.persona.email.length < 2;
    },
  },
};
</script>

<style>
form {
  margin-bottom: 2rem;
}
</style>